    position: absolute;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#fd741b), to(#a300bb));
    background-image: -webkit-linear-gradient(#fd741b, #a300bb);
    background-image: linear-gradient(#fd741b, #a300bb);
